import * as React from 'react';
import Box from '@mui/material/Box';
import Modal from '@mui/material/Modal';
import Button from '@mui/material/Button';
import Typography from '@mui/material/Typography';

const style = {
    position: 'absolute' as 'absolute',
    top: '50%',
    left: '50%',
    transform: 'translate(-50%, -50%)',
    width: 400,
    bgcolor: 'background.paper',
    border: '2px solid #000',
    boxShadow: 24,
    pt: 2,
    px: 4,
    pb: 3,
};

export default function NestedModal() {
    const [open, setOpen] = React.useState(false);
    const handleOpen = () => {
        setOpen(true);
    };
    const handleClose = () => {
        setOpen(false);
    };

    return (
        <div>
            <Button variant="contained" onClick={handleOpen}> Desejo Enviar Nome de Empresa</Button>
            <Modal
                open={open}
                onClose={handleClose}
                aria-labelledby="parent-modal-title"
                aria-describedby="parent-modal-description"
            >
                <Box sx={{ ...style, width: 400 }}>
                    <h2 id="parent-modal-title">Agradecemos seu apoio a nossa causa!</h2>
                    <p id="parent-modal-description">
                        Nos envie um email com o nome e informações da empresa na sua cidade, a região, se ela é nacional ou regional e alguma prova como fotos, prints ou links XD
                    </p>
                    <p>Nosso email é:</p>
                    <Typography gutterBottom variant="h5" component="h2">
                    patriabrasil@gmx.com
                    </Typography>
                    <Button variant="contained" onClick={handleClose}> Voltar</Button>
                </Box>
            </Modal>
        </div>
    );
}
